html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; }

.headerofsite {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, #EECFBA, #C5DDE8); }
  .headerofsite .h2 {
    position: absolute;
    color: white;
    top: 45%;
    width: 100%; }
    .headerofsite .h2 h2 {
      color: #000; }

section {
  position: relative;
  height: 100%; }
  section h2.text-center {
    color: #000;
    border: none;
    text-shadow: none;
    margin: 50px 0 35px; }
  section div.container {
    display: block;
    position: relative; }
    section div.container div.row {
      display: block;
      position: relative; }
      section div.container div.row .row {
        flex-direction: row;
        justify-content: center !important; }
        section div.container div.row .row li {
          list-style: none;
          align-items: center;
          padding: 15px; }
          section div.container div.row .row li a {
            text-decoration: none;
            color: forestgreen; }
      section div.container div.row div.layout {
        position: absolute;
        width: 100%;
        height: 80vh;
        padding: 15px;
        overflow: hidden; }
        section div.container div.row div.layout div.free-wall {
          width: 100%;
          height: 100%; }
          section div.container div.row div.layout div.free-wall div.brick img {
            max-width: 300px; }

@keyframes start {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="init"] {
            display: none; }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="start"] {
            display: block;
            animation: start 0.5s; }
          section div.container div.row div.layout div.free-wall .free-wall .brick[data-state="move"] {
            transition: top 0.5s, left 0.5s, width 0.5s, height 0.5s; }

ul.controls {
  font-size: 15px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  padding: 0; }
  ul.controls li.control {
    position: relative;
    list-style: none;
    padding: 10px;
    cursor: pointer;
    transition: background 150ms; }

.mixitup-control-active[data-filter]:after {
  background: transparent; }

.control {
  color: #28c700; }
  .control:first-of-type {
    border-radius: 3px 0 0 3px; }
  .control:last-of-type {
    border-radius: 0 3px 3px 0; }

.control[data-filter] + .control[data-sort] {
  margin-left: .75rem; }

.galery_container {
  padding: 1rem; }
  .galery_container:after {
    content: '';
    display: inline-block;
    width: 100%; }

.mix, .gap {
  display: inline-block;
  vertical-align: top; }

.mix {
  position: relative; }
  .mix:before {
    content: '';
    display: inline-block;
    padding-top: 56.25%; }

/* Grid Breakpoints */
/* 2 Columns */
.mix, .gap {
  width: calc(100% / 2 - (((2 - 1) * 1rem) / 2)); }

/* 3 Columns */
@media screen and (min-width: 541px) {
  .mix, .gap {
    width: calc(100% / 2 - (((3 - 1) * 1rem) / 3)); } }

/* 4 Columns */
@media screen and (min-width: 961px) {
  .mix, .gap {
    width: calc(100% / 2 - (((4 - 1) * 1rem) / 4)); } }

/* 5 Columns */
@media screen and (min-width: 1281px) {
  .mix, .gap {
    width: calc(100% / 3 - (((5 - 1) * 1rem) / 5)); } }
